/* ADD HOVER EFFECTS*/
.socialBtn {
  background: #000000;
  color: #ffffff;
  -webkit-transition-property: background 1s, border 2s; /* For Safari 3.0+ */
  transition: background 1s, border 2s; /* Standard syntax */
}
.socialBtn:hover {
  background: #64c1e4;
  color: #ffffff;
}
